import React from "react";
import { Footer, Row, Input, Button } from "react-materialize";

const Foot = () => (
  <Footer
    copyrights= " Â© 2019 All Rights Reserved. TY Conference."
    moreLinks={
      <a className="grey-text text-lighten-4 right" href="#!">
        More Links
      </a>
    }
    className="footer-sect"
  >
    <h5 className="white-text">Testimonies</h5>
    <Row>
    <form action="https://formspree.io/tyconference2017@gmail.com" method="POST" id="form-box">
      <Input s={12} placeholder="Full Name" name="name" className="form-input" />
      <Input type="email" placeholder="E-mail" s={12} name="Email" className="form-input" />
      <Input type="textarea" s={12} placeholder="Type your testimony here..." name="Message" className="form-input" id="textarea" /><br/>
      <div id="form-submit"><Button type="submit" >POST</Button></div>
    </form>


      
    </Row>
    <p className="grey-text text-lighten-4">
      Share your awesome testimony stories directly with us!
    </p>
  </Footer>
);

export default Foot;
